.show-filter {
  display: inline-block;
  width: 300px;
  height: 700px;
  border: 1px solid #000;
  position: sticky;
  top: 0;
  margin-top: 5px;
}

.hide-filter {
  display: none;
}

.filter-container {
  display: block;
  padding: 30px 0;
  height: 50px;

  li {
    display: inline-block;
    padding: 5px 10px;
  }

  .sort-filter {
    margin-right: 10px;
  }

  .bops-filter {
    background: transparent;
  }

  .item-count {
    float: right;
  }

  .grid-select {
    float: right;
  }

  button {
    background: transparent;
    border: 1px solid #000;
    padding: 5px 10px;
  }

  .col-switch {
    float: right;
  }

  .small-cols {
    background: transparent;
    border: none;
  }

  .lrg-cols {
    background: transparent;
    border: none;
  }

  .active-cols {
    border: 1px solid #000;
  }
}

.grid-select-container {
  padding-top: 10px;
}

.hide-filter-btn {
  background: transparent;
  width: 100%;
  height: 50px;
}

